

main {
  padding: 0 2rem 0 4rem;
  background-color: white;
}

canvas {
	position: absolute;
	// width: 100%;
	// height: 100vmin;
	top: 0;
	left: 0;
}

section {
  min-height: 75vh; //100vh;
  display: flex;
  align-items: center;
  // border: 1px dotted gray;
  div.slide {
    display: block;
    margin-left: 10rem;
    padding-left: 2rem;
    padding-right: 1rem;
    padding-top: 2rem;
    margin-top: 4rem;
    min-width: calc(100% - 10rem);
    // height: 70vmin;
    // padding-top: 16rem;
    z-index: 100;
  }
  &:not(.scrolled-in) div.slide {
    p, span {
      transform: translateY(2rem);
    }
  }
}


.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1rem;
  margin-right: -1rem;
  @each $v in (10,15,20,25,30,33,40,50,55,60,66,70,75,80,90,100) {
    .col-#{$v} {
      display: block;
      padding-left: 1rem;
      padding-right: 1rem;
      max-width: 100%*($v)/100;
      flex: 0 0 100%*($v)/100;
    }
  }
}

@media (max-width: $mobile-breakpoint - .2px) {
  section {
    padding-top: 10rem;
    min-height: 0;
    div.slide {
      font-size: 3rem;
      margin: 0;
      height: auto;
      // min-height: 100vh;
    }
  }
  .row {
    margin-left: -2rem;
    margin-right: -2rem;
      @each $v in (10,15,20,25,30,33,40,50,55,60,66,70,75,80,90,100) {
      .mcol-#{$v} {
        display: block;
        padding-left: 2rem;
        padding-right: 2rem;
        max-width: 100%*($v)/100;
        flex: 0 0 100%*($v)/100;
      }
    }
  }
}
