header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff; //linear-gradient(to bottom, white, white 70%, transparent);
  // padding-bottom: 5rem;
  z-index: 1000;
  a {
    text-decoration: none;
    text-transform: uppercase;
    white-space: nowrap;
  }

  &.about {
    background: transparent;
  }
  &.scrolled-in, &.header {
    background: transparent;
    nav#main {
      .logo {
        position: absolute;
        width: 100%;
        // padding-right: 8rem;
        // margin: 0;
        top: calc(50vh - 1em);
        left: 0;
        font-size: 10.25rem;
        text-align: center;
        //  margin-bottom: 1rem;
        p.title {
          font-size: 2.7rem;
          // transition:  all 0.5s ease-in-out;
          margin-top: -2rem;
          opacity: 1;
        }
      }
      #menu {
        // margin-left: 10.9rem;
        .dots {
          span {
            opacity: 0;
            font-size: 0;
            // display: none;
          }
          span:nth-child(-n+3) {
            opacity: 1;
            font-size: 1em;
            // display: inline-block;
          }
        }
        &:hover>div {
          .dots span {
            display: inline-block;
            opacity: 1;
            font-size: 1em;
            // transition:  all 0.25s ease-in-out;
          }
        }
      }
    }
  }

  nav#main  {
    // position: relative;
    margin-left: 0rem;
    width: 100%;
    // height: 3.5rem;
    display: flex;
    align-items: center;
    align-content: center;
    padding: 1rem 3rem 1rem 4rem;
    // background: #fff; //linear-gradient(to bottom, white, white 70%, transparent);

    .logo {
      position: fixed;
      display: block;
      flex: 0 0;
      top: 2rem;
      left: 3.8rem;
      // margin-right: 3.25rem;
      width: 8rem;
      text-align: center;
      transition:  all 0.5s ease-out;
      a {
        padding: 0;
        color: black;
      }
      p.title {
        font-size: 0;
        font-weight: 600;
        transition:  all 0.5s ease-out;
      }

    }
    #menu-c {
      opacity: 0;
      font-size: 0;
    }
    #menu {
      margin-left: 10.9rem;
      display: flex;
      align-items: center;
      flex: 1 1;
      min-height: 3em;
      >div{
        border-radius: 2em;
        transition:  all 0.25s ease-in-out;
        cursor: default;
        display: flex;
        align-items: center;
        padding: 0 1em;
        .dots {
          cursor: default;
          display: flex;
          align-items: center;
          align-content: center;
          justify-content: stretch;
              // margin-top: .5rem;
          margin-right: .4em;
          font-size: 2.5em;
          line-height: 0;
          span {
            cursor: default;
            // padding-top: 0.5em;
            // margin-right: -0.125em;
            margin-bottom: 0;
            margin-left: .125em;
            margin-right: .125em;
            display: inline-block;
            opacity: 1;
            transition:  all 0.25s ease-in-out;

          }
          span.active {
            opacity: 1;
            color: theme-color("primary");
          }
        }
        .links {
          display: flex;
          align-items: center;
          align-content: center;
          justify-content: stretch;
          line-height: 1;
          a.active {
            // display: inline-block;
            font-size: 1em;
            opacity: 1;
            transition:  all 0.25s ease-in-out;

          }
          a {
            // display: none;
            font-size: 0;
            opacity: 0;
            padding: 1em 0 .9em;
            margin-right: 0;//1rem;
            transition:  all 0.25s ease-in-out;
          }
        }
        a {
          // flex: 0 0;
          font-weight: bold;
          color: black;
          margin: 0;
          // padding: 0;
          transition:  all 0.25s ease-in-out;

        }
      }
      &:hover>div {
        background-color: theme-color("primary");
        border-radius: 2em;
        color: white;
        transition:  all 0.25s ease-in-out;
        a {
          display: inline-block;
          opacity: 1;
          min-width: initial;
          // transition:  all 0.25s ease-in-out;
        }
        .dots {
          span, span.active {
            display: inline-block;
            color: white;
            // padding-top: 0.5em;
          }
        }
        .links {
          a {
            margin-right: 1.25em;
            font-size: 1em;
          }
          a.active {
            display: inline-block;
            color: white;
            font-size: 1em;

          }
        }
      }
    }
    a {
      font-size: 1em;
      display: inline-block;
      position: relative;
      // margin-left: auto;
      font-weight: bold;
      color: black;
      &.dot {
        padding: 1em;
        // padding-right: 2rem;
        &::before {
          content: "";
          display: block;
          position: absolute;
          width: 0;
          height: 0;
          padding: 1em;
          margin-top: 0rem;
          top: 0.5em;
          right: 0.25em;
          z-index: -1;
          border-radius: 100%;
          background-color: theme-color("primary");
          transition: all .25s ease-in-out;
        }
      }
      &.dot:hover::before {
        transform: scale($base-hover-scale);
        // padding: 1.25rem;
        // top: 0.5rem;
        // right: -.125rem;
      }
    }
  }
  @media (max-width: $mobile-breakpoint - .2px) {
    nav#main  {
      padding: 2rem 2rem;
      font-size: 2.5rem;
      align-items: flex-start;
      #menu {
        margin-left: 0;
        > div {
          width: 100%;
          .dots {
            font-size: 2.75em;
            padding: 3.5rem 0;
            span {
              font-size: 0;
            }
            span.active {
              font-size: 1em;
              // color: $black;
            }
          }
          .links {
            flex-direction: column;
            align-items: flex-start;
            a {
              &.active {
                color: black;
              }
            }
          }
        }
        &:hover>div {
          background-color: transparent;
          border-radius: 2em;
          // color: white;
          .dots span {
            &.active {
              opacity: 1;
              color: theme-color("primary");
            }
          }
          .links a {
            opacity: 0;
            font-size: 0;
            color: black;
            &.active {
              color: black;
              font-size: 1em;
              opacity: 1;
            }
          }
        }
      }
      .logo {
        opacity: 0;
        z-index: -1000;
      }
      &.open {
        width: 100vw;
        height: 100vh;
        background-color: theme-color("primary");
        justify-content: space-between;
        #menu-c {
          opacity: 1;
          font-size: initial;
          padding: .5em;
        }
        #menu {
          position: absolute;
          top: 5em;
          left: 1em;
          >div {
            .dots span {
              font-size: 0 !important;
              opacity: 0 !important;
            }
            .links a {
              font-size: 2em;
              opacity: 1;
              &.active {
                color: white;
              }
            }
          }
          &:hover>div {
            background-color: transparent;
            border-radius: 0;
            // color: white;
            .links a {
              opacity: 1;
              font-size: 2em;
              &.active {
                color: white;
              }
            }
          }
        }
        .logo {
          opacity: 1;
          z-index: 1000;
          position: relative;
          top: 2rem;
          left: 3.8rem;
          width: 8rem;
          font-size: 1em;
          p.title {
            opacity: 0;
            font-size: 0;
          }
        }
      }
    }
    &.about {
      background: white;
    }
    &.scrolled-in, &.header {
      nav#main  {
        .logo {
          opacity: 1;
          z-index: 1000;
            p.title {
            font-size: 4.7rem;
            // transition:  all 0.5s ease-in-out;
            margin-top: 1rem;
          }
        }
        #menu:hover {
          > div {
            width: 100%;
            .dots {
              span {
                font-size: 0;
              }
              span.active {
                font-size: 1em;
                // color: $black;
              }
            }
          }
        }
      }
    }
  }
}


nav#side {
  z-index: 1000;
  transition: all .25s ease-in-out;
  a.btn-outline-circle {
    transform: scale(1);
    transition: all .25s ease-in-out;
    i {font-size: 2rem;}
    &.hover-caption:hover {
      i {
        opacity: 0;
        font-size: 0;
      };
    }
  }

  &.header {
    left: 12rem;
    >div {
      a#up {
        opacity:0;
      }
      a#down {
        &, &::before {
          border: $button-o-border-width solid transparent; //.125rem theme-color("primary") solid;
          border-radius: 100%;

        }
        margin-top: 8rem;
        background-color: transparent;
        background-size: 0 0;
        // padding: 0;
        &:hover {
          font-size: 2*$button-font-size;
          &::before {
            background-color: transparent;
            border: none;
          }
          &::after {
            content: "";
            transform: rotate(0deg);
          }
          i {
            display: block;
            font-size: 1em;
            opacity: 1;
            // transform: translateY(1rem);
            animation: bounce 1s cubic-bezier(0.5, -0.5, 0.7, 1) infinite;
          }
        }
      }
    }
  }
  &.contact {
    >div a#down {
      background-size: 200% 200%;

      // background-color: theme-color("primary");
      border-color: theme-color("primary");
      i {
        font-size: 0;
        opacity: 0;
      }
      &::after,&:hover::after {
        content: "";
        transform: rotate(0deg);
        // font-size: $button-font-size;
      }
    }
  }
  position: sticky;
  left: 4rem;
  bottom: 2rem;
  width: 8rem;
  font-size: $button-font-size;
  >div {
    position: absolute;
    bottom: 0;
    width: 8rem;
    padding: 2.5rem 1rem;
    a.btn-outline-circle {
      position: relative;
      // font-size: $button-font-size;
      display: block;
      // margin: .5em 0;
      // padding: 2px;
      color: black;
      // &::before {
      //   // content: "";
      //   // padding: 50%;

      // }
      // &::after {
      //   font-size: .5em;
      //   text-transform: uppercase;
      //   transform: rotate(90deg);
      //   position: absolute;
      //   top: 0;
      //   left: 0;
      //   width: 100%;
      //   line-height: 6em;
      //   text-align: center;
      //   // border: 1px solid #000;
      // }
      &:hover {
        i {opacity: 0;};
      //   &#up::after {
      //     content: "Prev";
      //   }
      //   &#down::after {
      //     content: "Next";
      //   }
      }
    }
  }
}

@media (max-width: $mobile-breakpoint - .2px) {
  nav#side {
    left: -100%;
    display: none;
    &.header {
      display: block;
      left: calc(50vw - 4rem);
      & > div {
        a#down i,
        a#down:hover i {
          font-size: 7rem;
        }
      }
    }
  }
}

@keyframes bounce {
  0%   { transform: translateY(0); }
  // 10%  { transform: translateY(.1rem); }
  50%  { transform: translateY(1rem); }
  // 90%  { transform: translateY(.1rem); }
  // 95%  { transform: translateY(-.1rem); }
  100% { transform: translateY(0); }
}
