#login {
  position: fixed;
  top:0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  // display: flex;
  // align-items: center;
  overflow: hidden;
  z-index: 100000;
  opacity: 1;
  transition: opacity .3s ease-in-out, z-index .3s ease-in-out;
  div.login-wrapper {
    position: absolute;
    min-height: 200vmax;
    min-width: 200vmax;
    border-radius: 100%;
    padding: calc(100vmax - 50vh) calc(100vmax - 50vw);
    bottom: calc(-100vmax + 50vh);
    left: calc(-100vmax + 50vw);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #000;
    color: #fff;
    transform: scale(1);
    transition: all ease-in-out .4s;
    // border: 1px dotted gray;
   
  }
  div.header {
    min-width: 100%;
    display: flex;
    align-items: center;
    align-content: center;
    padding: 1rem 4rem 1rem 4rem;
    opacity: 1;
    transition: all .25s ease-in-out .3s;
    .logo {
      // position: fixed;
      display: block;
      flex: 0 0;
      // top: 2rem;
      // left: 3.8rem;
      margin-right: auto;
      padding: 1rem;
      width: 8rem;
      text-align: center;
      a {
        text-decoration: none;
        text-transform: uppercase;
        color: black;
        white-space: nowrap;
      }
    }
    a.dot {
      // padding-right: 2rem;
      position: relative;
      padding: 1em;
      text-decoration: none;
      text-transform: uppercase;
      color: black;
      white-space: nowrap;
    &::before {
        content: "";
        display: block;
        position: absolute;
        width: 0;
        height: 0;
        padding: 1em;
        margin-top: 0rem;
        top: 0.6125em;
        right: 0.25em;
        z-index: -1;
        border-radius: 100%;
        background-color: theme-color("primary");
        transition: all .25s ease-in-out;
      }
      &:hover::before {
        transform: scale($base-hover-scale);
        // padding: 1.25rem;
        // top: 0.5rem;
        // right: -.125rem;
      }
    }
  }
  div.slide {
    width: 100%;
    height: 100%;
    opacity: 1;
    transition: all .25s ease-in-out .3s;
  }
  input {
    margin-bottom: 2rem;
    border-bottom-color: #fff;
    color: #fff;
    font-size: 1.2em;
    font-weight: 600;
    &:focus, &:active, &:hover {
      outline: none;
      border: none;
      border-bottom: 2px theme-color("primary") solid;
    }
  }
  .btn-outline-circle {
    color: #fff;
    transform: scale(1);
  }
  &.hidden {
    z-index: -1;
    // opacity: 0;
    div.login-wrapper {
      bottom: calc(-100vmax + 100vh - 2.5rem);
      left: calc(-100vmax + 100vw - 4.5rem);
      transform: scale(0);
      transition: all .4s ease-in-out .2s;
    }
    div.header, div.slide {
      opacity: 0;
      transition: all .25s ease-in-out;

    }
    transition: opacity .25s ease-in-out .3s, z-index .3s ease-in-out .3s;
  }
  @media (max-width: $mobile-breakpoint - .2px) {
    font-size: 3rem;
  }
}

