    #fundrising-time {
      padding: 0;
      >p {
        display: flex;
        flex: 0 0 50%;
        align-items: flex-start;
        margin-top: 1em;
        margin-bottom: 1em;
        >span {
          font-size: 5em;
          font-weight: 400;
          line-height: 0.8;
          margin-bottom: 1rem;
        }
        >span + span {
          font-size: 2em;
          font-weight: 600; 
          line-height: 1;
        }
      }
      >p + p {
        margin-left: .75em;
      }
    }

    // padding-top: 10rem;
    div.dot {
      &::after {
        position: absolute;
        content: "Click";
        font-weight: 600;
        display: block;
        text-transform: uppercase;
        text-align: center;
        transform: translate(-50%, -50%);
        opacity: 0;
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        transition: all .25s ease-in-out;
    }
      &:hover, &:focus {
        cursor: pointer;
        transform: scale($base-focus-scale);
        &::after {
          opacity: 1;
        }
      }
      &.clicked {
        &:hover, &:focus {
          transform: scale($base-focus-scale);
          &::after {
            position: absolute;
            // content: "";
            display: block;
          }
        }
        &::after {
          position: absolute;
          content: "";
          display: block;
          width: 75%;
          height: 75%;
          text-transform: uppercase;
          text-align: center;
          transform: translate(-50%, -50%);
          border-radius: 100%;
          background-image: url("/assets/img/coin.png");
          background-position: center;
          background-size: contain;
          background-repeat: no-repeat;
          opacity: 1;
          transition: all .25s ease-in-out;
        }
        &.coin {
          background-color: theme-color("primary");
          &::after {
            background-image: url("/assets/img/coin.png");
          }
        }
        &.watch {
          background-color: theme-color("secondary");
          border-color:  theme-color("secondary");
          &::after {
            background-image: url("/assets/img/watch.png");
          }
        }
