#problem1 {
  .slide {
    padding-top: 3rem;
    margin-top: 4rem;
    div {
      > .dot {
        margin-bottom: 3rem;
        &:nth-of-type(1) {
          &::after {
            content: "";
            position: absolute;
            display: block;
            height: 100%;
            width: 80%;
            background-image:
              url(/assets/img/coin_shadow_yellow.png),
              url(/assets/img/coin.png);
            background-position:
              center 105%,
              center top;
            background-repeat: no-repeat;
            background-size: 70%;
            transform: translate(-50%, -50%);
            transition: all .25s ease-in-out;
            opacity: 0;
          }
        }
        &:nth-of-type(2) {
          background-color: transparent;
          background-image: radial-gradient(theme-color("primary") 50%, transparent 50%, transparent 100%);
          background-position: center;
          background-repeat: no-repeat;
          background-size: 200% 200%;
          transition: all .25s ease-in-out;
          &::after {
            position: absolute;
            content: "Now, it's easy";
            font-weight: 600;
            display: block;
            text-transform: uppercase;
            text-align: center;
            transform: translate(-50%, -50%); // rotate(-90deg);
            opacity: 0;
            transition: all .25s ease-in-out;
          }
        }
      }
      > .tooltip {
        top: auto;
        left: auto;
        bottom: 45%;
        right: -50%;
        line-height: 1.47;
        border-radius: 3rem;
        padding: 0.75rem 1.75rem;
        &::before {
          left: 25%;
        }
        &::after {
          left: calc(25% + #{$base-border-width});
        }
      }
      &:hover {
        >.dot {
          &:nth-of-type(1) {
            &::after {
              content: "";
              position: absolute;
              display: block;
              height: 100%;
              width: 80%;
              background-image:
                url(/assets/img/coin_shadow_yellow.png),
                url(/assets/img/coin.png);
              background-position:
                center 105%,
                center top;
              background-repeat: no-repeat;
              background-size: 80%;
              transform: translate(-50%, -60%);
              opacity: 1;
            }
          }
          &:nth-of-type(2) {
            border: $base-border-width solid theme-color("primary");
            background-size: 0 0;
            &::after {
              opacity: 1;
            }
          }
        }
      }
    }
  }
}
