#solution3 {
  .slide {
    .s3_targets {
      position: absolute;
      opacity: 0;
      // font-size: 0;
      transition: all 0.35s ease-in-out;
      transform: translateY(2rem);
      &.active {
        opacity: 1;
        // font-size: initial;
        transform: translateY(0);
        transition: all 0.35s ease-in-out .25s;
      }
    }
    [id^='s3-arrow'] {
      // border: 1px dotted gray;
      position: absolute;
      &.left {
        margin-left: 7rem;
        margin-top: 3.5rem
      }
      &.right {
        margin-right: 7rem;
        margin-top: 3.5rem
      }
    }
    svg, video {
      transition: all 0.25s ease-in-out;
    }
    .btn-face {
      .bg {
        margin: 0;
        padding: 50%;
        background-size: 100%;
        transition: all 0.25s ease-in-out;
      }
    }
    .success .btn-face {
      .bg {
        background-color: theme-color("secondary");
        border-color: theme-color("secondary");
        &.face-1 {
          background-color:  theme-color("primary");
          border-color:  theme-color("primary");
          + video {
            opacity: 1;
          }
        }
      }
      video {
        opacity: 0;
      }
    }
    .failure .btn-face {
      .bg {
        background-color: theme-color("primary");
        border-color: theme-color("primary");
        &.face-1 {
          background-color: theme-color("secondary");
          border-color: theme-color("secondary");
          + video {
            opacity: 0;
          }
        }
      }
      video {
        opacity: 1;
      }
    }
  }
}
