.row {
  // align-items: stretch;
  &:hover {
    // padding: 10% 1rem;
    .btn-outline-image.add {
      background-color: $button-hover-bg-color;
      border-color: $button-hover-bg-color;
      // display: block;
      max-width: 80%;
      margin: 10% 10% calc(10% + 1rem);
      padding: 40% 0;//calc(40% - #{$button-border-width}) 0;
      &:hover {
        max-width: 100%;
        margin: 0;
        padding: calc(50% - #{$button-border-width}) 0;
        background-color: $button-bg-color;
        border-color: $button-bg-color;
        margin-bottom: 1rem;
        &::after {
          margin: calc(50% + 1em) auto 0;
        }
      }
      &::after {
        margin: calc(50% + 1em + 1rem) auto 0;
      }
    }
  }
}

