

.btn-outline-circle {
  cursor: pointer;
  position: relative;
  width: 100%;
  height: $button-o-font-size;
  font-size: $button-o-font-size;
  font-weight: 600;
  line-height: 0;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: $button-o-color;
  display: block;
  padding: calc(50%  - #{$button-o-border-width}) 0;
  margin-bottom: 1rem;
  border: $button-o-border-width solid $button-o-border-color;
  border-radius: 100%;
  // background-color: $button-o-bg-color;
  background-color: $button-o-bg-color;
  background-image: radial-gradient($button-o-hover-bg-color 50%, $button-o-bg-color 50%, $button-o-bg-color 100%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 0 0;
  transition: all .25s ease-in-out;
  transform: scale(0) rotate(0deg);
  &::after {
    position: relative;
    display: inline-block;
    // height: 0;//$button-font-size;
    font-size: $button-o-font-size;
    color: $button-o-hover-color;
    transform: rotate(-90deg);
  }
  &:focus {
    outline: 0;
  }
  &:hover, &.sending {
    border-color: $button-o-hover-border-color;
    background-size: 200% 200%;
    // background-color: $button-o-hover-bg-color;
    color: $button-o-hover-color;
    height: $button-o-font-size;
    transform: rotate(0deg);
    &.hover-caption {
      font-size: 0;
      height: $button-o-font-size;
      &::after {
        content: attr(data-caption);
      }
    }
  }
  &.sending.error {
    border-color: red;
  }
}


.btn-outline-image {
  cursor: pointer;
  position: relative;
  height: $button-font-size;
  max-width: 100%;
  font-family: sans-serif;
  font-size: $button-font-size;
  font-weight: 600;
  line-height: 0;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: $button-color;
  background-color: $button-bg-color;
  display: block;
  padding: calc(50% - #{$button-border-width}) 0;
  margin-bottom: 1rem;
  border: $button-border-width solid $button-border-color;
  border-radius: 100%;
  transform: scale(0) rotate(0deg);
  transition: all 0.35s ease-in-out;
  &.add {
    span {
      position: absolute;
      display: block;
      top: -6rem;
      left: 50%;
      font-size: 1rem;
      text-transform: none;
      line-height: 1;
      padding: 1.5rem;
      opacity: 0;
      background-color: white;
      border-radius: 2rem;
      border: 1px solid black;
      white-space: nowrap;
      &::after, &::before {
        position: absolute;
        display: block;
        content: "";
        border-color: transparent;
        border-style: solid;
      }
      &::before {
        bottom: calc(-1.5rem - .2em);
        border-width: calc(1.5rem + .2em) calc(1rem + .2em) 0 0;
        border-top-color: black;
      }
      &::after {
        bottom: calc(-1.5rem + .1em);
        left: calc(1.5rem + .13em);
        border-width: 1.5rem 1rem 0 0;
        border-top-color: #fff;
      }
    }
    // line-height: 0;
    // height: $button-font-size;
    // font-size: $button-font-size;
    &::after {
      content: $scramble-plus;
      font-family: "scramble" !important;
      font-weight: 400;
      position: relative;
      // content: attr(data-caption);
      display: block;
      font-size: $button-font-size;
      padding: 1rem; //calc(15% - .5em) 15%;
      margin: calc(50% + 1em) auto 0;
      border: $button-border-width solid $button-border-color;
      border-radius: 100%;
      line-height: .9;
      height: 2em;
      width: 2em;
      text-align: center;
      color: $button-color;
      background-color: $button-bg-color;
      transform: rotate(0deg);
    }
    &:hover {
      span {
        opacity: 1;
        &::after, &::before {
        }
      }
      &::after {
        transform: rotate(360deg+45deg);
      }
    }
  }

  &.coin {
    background-image: nth($button-bg-image, 1);
    background-size: 66%;
    background-position: center;
    background-repeat: no-repeat;
    &:hover {
      background-image: nth($button-hover-bg-image, 1);
      animation: bounce-bg 1s cubic-bezier(0.5,-0.5, 0.7, 1) infinite;
    }
    &:active, &:focus {
      background-image: nth($button-hover-bg-image, 1);
      animation: bounce-bg 1s cubic-bezier(0.5,-0.5, 0.7, 1) 1;
    }
  }
  @for $i from 1 through 7 {
    &.face-#{$i} {
      background-image: nth($button-bg-image, $i+1);
      background-size: 75%;
      background-position: center;
      background-repeat: no-repeat;
      &:hover {
        background-image: nth($button-hover-bg-image, $i+1);
      }
    }
  }
}
.btn-add {
  position: relative;
  // content: attr(data-caption);
  display: block;
  // font-size: $button-font-size;
  // cursor: pointer;
  padding-bottom: 4em;
  padding-left: 2em;
  transform: scale(0);
  transition: all 0.35s ease-in-out;
  &::after {
    cursor: pointer;
    position: absolute;
    content: $scramble-plus;
    font-family: "scramble" !important;
    font-weight: 400;
    font-size: 2*$button-font-size;
    line-height: 1.5em;
    text-align: center;
    left: calc(50% - .75em);
    bottom: .5em;
    width: 1.5em;
    height: 1.5em;
    border-radius: 100%;
    border: $button-border-width solid $button-border-color;
    color: $button-color;
    background-color: $button-bg-color;
    transition: all 0.35s ease-in-out;
    &:hover {
      transform: scale($base-hover-scale);
    }
    }
  &.visible {
    &::after {
      transform: rotate(45deg);
    }
    &:hover {
      &::after {
        transform: rotate(45deg) scale($base-hover-scale);
      }
    }
  }
}

.btn-face {
  position: relative;
  transform: scale(0);
  transition: all 0.35s ease-in-out;
  &:hover {
  }
  &.playing {
    .tooltip {
      opacity: 1;
    }
  }
  &.selected {
    .add {
      &::after {
        transform: rotate(45deg);
      }
      &:hover::after {
        transform: rotate(45deg) scale($base-hover-scale);
      }
    }
  }
  .bg {
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    padding: 40%;
    margin: 10%;
    background-color: theme-color("secondary");
    border-radius: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
    @for $i from 1 through 7 {
      &.face-#{$i} {
        background-image: nth($button-bg-image, $i+1);
      }
    }
  }
   video {
    position: relative;
    border-radius: 100%;
    // border: $base-border-width solid theme-color("primary");
    width: 100%;
    height: 100%;
    opacity: 100%;
    transform: scale(1);
    transition: transform 0.25s ease-in-out .25s, opacity 0.25s ease-in-out;
  }
  .add {
    padding-bottom: 5em;
    &::after {
      cursor: pointer;
      position: absolute;
      content: $scramble-plus;
      font-family: "scramble" !important;
      font-weight: 400;
      font-size: 2*$button-font-size;
      line-height: 1.5em;
      text-align: center;
      left: calc(50% - .75em);
      bottom: .5em;
      width: 1.5em;
      height: 1.5em;
      border-radius: 100%;
      background-color: theme-color("primary");
      transform: scale(1);
      transition: all 0.35s ease-in-out;
    }
    &:hover::after {
      transform: scale($base-hover-scale);
    }
  }
  .tooltip {
    opacity: 0;
    left: 50%;
  }
}

.btn-face,
.btn-add,
.btn-outline-circle,
.btn-outline-image {
  &, &:hover {
    transform: scale(0);
  }
}


.scrolled-in {
  .btn-face,
  .btn-add,
  .btn-outline-circle,
  .btn-outline-image {
    &, &:hover {
      transform: scale(1);
    }
  }
}
@media (max-width: $mobile-breakpoint - .2px) {
  .btn-outline-circle {
    font-size: 1em;
  }
  .btn-face .add::after,
  .btn-add::after {
    font-size: 2em;
  }
}
@keyframes bounce-bg {
  0% { background-position-y: 50%; }
  50% { background-position-y: 0%; }
  100% { background-position-y: 50%; }
}
