
footer {
  padding: 2em 2rem 2em 5rem;
  background-color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1rem;
  font-weight: 600;
  color: white;
  a {
    text-decoration: none;
    text-transform: uppercase;
    white-space: nowrap;
    color: white;
  }
  .copyright {
    padding-right: 1rem;
    margin-right: auto;
    // display:flex;
    // margin-left: auto;
    // margin-right:3rem; 
  }
  .logo {
    width: 8rem;
    margin-right: 3rem;
  }
  ul.social {
    padding-right: 1rem;
    // margin-left:3rem;
    li {
      display: inline-block;
      list-style: none;
      text-transform: uppercase;
      // margin-bottom: 1rem;
      margin: .5rem;
      font-size: 2em; 
    }
  }
  .address {
    padding-right: 1rem;
    margin-right: auto;
    // margin-left: auto;
    // margin-right:3rem; 
  }
  .muted {
    color: #4d4d4d;
  }
  .credits {
    display: none;
  }
  @media (max-width: $mobile-breakpoint - .2px) {
    font-size: 3em;
    flex-direction: column;
    align-items: flex-start;
    ul.social {
      // padding: 0 1rem;
      position: absolute;
      right: 0;
    }
    .logo {
      margin-bottom: 3em;
      order: 1;
    }
    .address {
      margin-bottom: .5em;
      order: 2;
    }
    .copyright {
      margin-bottom: .5em;
      order: 3;
    }
  }  
}

