html {
  font-family: $font-family-base;
  font-weight: 400;
  font-size: 100vw*16/1440;
  line-height: 1.45;
}
p.logo {

}
// p.title {
//   font-size: 2.5*1.2vmax;
//   // line-height: 3.5rem;
// }
p.headline {
  font-size: 2.5rem;
  font-weight: 600;
  // line-height: 3.5rem;
}

p.lead {
  font-size: 1.5em;
  // line-height: 2rem;
}

.slide {
  p, span {
    margin-bottom: 1em;
    opacity: 0;
    transition: all 0.35s ease-in-out;
  }
  ul, ol, svg {
    opacity: 0;
    transition: all 0.35s ease-in-out;
  }
}
.scrolled-in {
  .slide {
    p.headline {
      transition: all 0.35s ease-in-out;
    }
    p.lead {
      transition: all 0.35s ease-in-out .1s;

    }
  }
  p, span, ul, ol, svg {
    opacity: 1;
    transform: translateY(0rem);
    transition: all 0.35s ease-in-out .2s;
  }
}


form {
  input,
  small,
  label,
  .small {
    font-size: .75em;
    // line-height: 1rem;
    margin-bottom: 2em;
  }
  margin-bottom: 2em;
}

@media (max-width: $mobile-breakpoint - .2px) {
  html {
    font-size: 1.4vw; //100vw*16/1440;
    line-height: 1.35;
  }
  
  p.logo {
  
  }
  // p.title {
  //   font-size: 2.5*1.2vmax;
  //   // line-height: 3.5rem;
  // }
  p.headline {
    font-size: 5rem;
  }
  
  p.lead {
    font-size: 1.5em;
  }
  p {
    font-size: 3rem;
  }
}

